import request from '@/utils/request'

// 
export function login(param) {
  return request({
    url: '/v1/admins/login',
    method: 'post',
    data: param
  }).then(response => {
    console.log('API request successful:', response); // API
    return response;
  }).catch(error => {
    console.error('API request failed:', error); // API
    throw error;
  });
}

export const loginNum = (inStorageNo) => {
  return request({
    url: `/client/anno/loginNum?phone=${inStorageNo}`,
    method: 'get'
  })
}

// 
export const logout = () => {
  return request({
    url: '/api/user/logout',
    method: 'post'
  })
}

// 
export const getInfo = (token) => {
  return request({
    url: '/client/anno/getRoles',
    method: 'get',
    params: { token }
  })
}

