import request from '@/utils/request'

// 
export function getSwiperList() {
  return request({
    url: '/v1/carousels/getAllCarousels', // 
    method: 'get',
  }).then(response => {
    console.log('API request successful:', response); // API
    return response; // response
  }).catch(error => {
    console.error('API request failed:', error); // API
    throw error;
  });
}

